<template>
  <div class="Container-TaskInfomation">
    <!--  -->
    <el-breadcrumb class="breadcrumb" separator="/">
      <el-breadcrumb-item class="pathActive"></el-breadcrumb-item>
      <el-breadcrumb-item class="active"></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="head-btn">
      <div class="oper-btns-left">
        <div class="refresh">
          <el-button icon="el-icon-refresh" @click="refresh"
            >
          </el-button>
        </div>
      </div>
      <div class="oper-btns-right" v-if="['ROOT', 'ADMIN'].includes(userRole)">
        <el-button
          class="bigdel-btn"
          icon="el-icon-delete"
          @click="delectExtraInfo"
          ></el-button
        >
      </div>
    </div>
    <!-- table -->
    <el-table
      ref="multipleTable"
      :data="taskData"
      stripe
      border
      style="width:100%;"
      class="extraTable"
      @selection-change="handleDetailSelectionChange"
    >
      <el-table-column type="selection"></el-table-column>
      <el-table-column prop="id" label="ID" width="100"></el-table-column>
      <el-table-column
        prop="deadline"
        label=""
        width="120"
      ></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="side" label=""></el-table-column>
      <el-table-column
        prop="acceptedStandard"
        label=""
      ></el-table-column>
      <el-table-column prop="setting" label="" width="160">
        <template slot-scope="scope">
          <el-tooltip content="" effect="light" :enterable="false">
            <el-button
              icon="iconfont icon-xiangqing"
              @click="handleDetail(scope.$index, scope.row)"
            ></el-button>
          </el-tooltip>
          <el-tooltip
            content=""
            :enterable="false"
            v-if="['ROOT', 'ADMIN'].includes(userRole)"
          >
            <el-button
              icon="iconfont icon-shanchu"
              @click="handleDelete(scope.$index, scope.row)"
            ></el-button>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="block">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[8, 16, 24, 32, 40]"
        :page-size="8"
        layout="sizes,total, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "DeviceInformation",
  components: {},
  data() {
    return {
      userRole: "", //
      userID: "",
      taskData: [],
      checkedDetail: null,
      // 
      currentPage: 1,
      page: 1,
      size: 10,
      total: 0,
      nextDateDay: "",
    };
  },
  methods: {
    //
    handleDetailSelectionChange(selection) {
      this.checkedDetail = selection;
    },
    // 
    handleDetail(index) {
      console.log(this.taskData[index]);
      this.$router.push({
        path: "/taskDetailInfo",
        query: this.taskData[index],
      });
    },
    // 
    handleDelete(index) {
      let that = this;
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          let url =
            "http://47.102.214.37:8080/ops/schedule/" + that.taskData[index].id;
          console.log(url);
          axios.delete(url).then(() => {
            that.tableData.splice(index, 1);
            this.$message({
              message: "",
              type: "success",
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    // 
    delectExtraInfo() {
      let that = this;
      if (this.checkedDetail.length == 0) {
        that.$alert("", "", {
          confirmButtonText: "",
        });
      } else {
        that
          .$confirm(", \n?", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning",
          })
          .then(() => {
            that.checkedDetail.forEach((element) => {
              that.taskData.forEach((e, i) => {
                if (element.id == e.id) {
                  console.log(element, e, i);
                  let url = "http://47.102.214.37:8080/ops/schedule/" + e.id;
                  console.log(url);
                  axios.delete(url).then(() => {
                    that.tableData.splice(i, 1);
                    this.$message({
                      message: "",
                      type: "success",
                    });
                  });
                }
              });
            });
          })
          .catch(() => {
            this.$message.info("");
          });
      }
    },
    // 
    handleSizeChange(val) {
      // console.log(` ${val} `);
      let that = this;
      console.log(val);
      that.size = val;
      let url =
        "http://47.102.214.37:8080/ops/schedule?page=0" + "&size=" + that.size;
      console.log(url);
      axios.get(url).then((res) => {
        console.log(res.data);
      });
    },
    // 
    handleCurrentChange(val) {
      let that = this;
      that.page = val;
      that.currentPage = val;
      console.log(that.page);
    },
    // 
    refresh() {
      let that = this;
      console.log("aaa");
      that.userRole = "";
      that.userID = "";
      that.total = 0;
      that.taskData = [];
      axios.get("http://47.102.214.37:8080/user/me").then((res) => {
        console.log(res.data);
        that.userRole = res.data.role;
        that.userID = res.data.id;
      });
      setTimeout(() => {
        console.log("aaaa");
        if (["ROOT", "ADMIN", "CREATOR"].includes(that.userRole)) {
          axios
            .get("http://47.102.214.37:8080/ops/schedule?page=0&size=10")
            .then((res) => {
              console.log(res.data);
              for (var i = 0; i < res.data.content.length; i++) {
                let taskID = res.data.content[i].id;
                let taskname = res.data.content[i].name;
                let taskside = res.data.content[i].side;
                let taskacceptedStandard = res.data.content[i].acceptedStandard;
                let url =
                  "http://47.102.214.37:8080/ops/schedule/status/" +
                  res.data.content[i].id;
                setTimeout(() => {
                  axios.get(url).then((res) => {
                    that.taskData.push({
                      id: taskID,
                      deadline: res.data.nextDateDay,
                      name: taskname,
                      side: taskside,
                      acceptedStandard: taskacceptedStandard,
                    });
                    that.total++;
                  });
                }, 300);
              }
              that.$message({
                message: "",
                type: "success",
              });
            });
        } else if (that.userRole == "OPERATOR") {
          let url = "http://47.102.214.37:8080/my/schedule";
          console.log(url);
          axios.get(url).then((res) => {
            console.log(res.data);
            for (var i = 0; i < res.data.length; i++) {
              let taskID = res.data[i].id;
              let taskname = res.data[i].name;
              let taskside = res.data[i].side;
              let taskacceptedStandard = res.data[i].acceptedStandard;
              let url =
                "http://47.102.214.37:8080/ops/schedule/status/" +
                res.data[i].id;
              setTimeout(() => {
                axios.get(url).then((res) => {
                  that.taskData.push({
                    id: taskID,
                    deadline: res.data.nextDateDay,
                    name: taskname,
                    side: taskside,
                    acceptedStandard: taskacceptedStandard,
                  });
                  that.total++;
                });
              }, 300);
            }
            that.$message({
              message: "",
              type: "success",
            });
          });
        }
      }, 500);
    },
  },
  created() {
    let that = this;
    axios.get("http://47.102.214.37:8080/user/me").then((res) => {
      console.log(res.data);
      that.userRole = res.data.role;
      that.userID = res.data.id;
    });
    setTimeout(() => {
      console.log("aaaa");
      if (["ROOT", "ADMIN", "CREATOR"].includes(that.userRole)) {
        axios
          .get("http://47.102.214.37:8080/ops/schedule?page=0&size=10")
          .then((res) => {
            console.log(res.data);
            for (var i = 0; i < res.data.content.length; i++) {
              let taskID = res.data.content[i].id;
              let taskname = res.data.content[i].name;
              let taskside = res.data.content[i].side;
              let taskacceptedStandard = res.data.content[i].acceptedStandard;
              let url =
                "http://47.102.214.37:8080/ops/schedule/status/" +
                res.data.content[i].id;
              setTimeout(() => {
                axios.get(url).then((res) => {
                  that.taskData.push({
                    id: taskID,
                    deadline: res.data.nextDateDay,
                    name: taskname,
                    side: taskside,
                    acceptedStandard: taskacceptedStandard,
                  });
                  that.total++;
                });
              }, 300);
            }
          });
      } else if (that.userRole == "OPERATOR") {
        let url = "http://47.102.214.37:8080/my/schedule";
        console.log(url);
        axios.get(url).then((res) => {
          console.log(res.data);
          for (var i = 0; i < res.data.length; i++) {
            let taskID = res.data[i].id;
            let taskname = res.data[i].name;
            let taskside = res.data[i].side;
            let taskacceptedStandard = res.data[i].acceptedStandard;
            let url =
              "http://47.102.214.37:8080/ops/schedule/status/" + res.data[i].id;
            setTimeout(() => {
              axios.get(url).then((res) => {
                that.taskData.push({
                  id: taskID,
                  deadline: res.data.nextDateDay,
                  name: taskname,
                  side: taskside,
                  acceptedStandard: taskacceptedStandard,
                });
                that.total++;
              });
            }, 300);
          }
        });
      }
    }, 500);
  },
};
</script>
